#mobile-container {
    position: fixed;
    top: 0;
    right: -100%;
    z-index: 9999;
    width: 100%;
    height: 100vh;
    background-color: rgb(212, 212, 212);
    transition: 0.7s;

    &.active {
        transform: translateX(-100%);
    }

    @media (min-width: 1024px) {
        display: none;
    }

    .head-wrapper {
        height: $headerHeight;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 12px;

        @media (min-width: 768px) {
            padding: 0 24px;
        }
    }

    .right {
        display: flex;
        align-items: center;

        .language {
            margin-right: 12px;
        }

        button {
            background-color: rgba(0, 0, 0, 0);
        }
    }

    .logo {
        img {
            width: 60px;

            @media (min-width: 768px) {
                width: 70px;
            }
        }
    }
}
