header#header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    width: 100%;
    height: $headerHeight;
    background-color: rgba(255, 255, 255, 0.5);
    padding: 0 12px;
    transition: 0.7s;

    &.flip {
        transform: translateX(-100%);
    }

    @media (min-width: 768px) {
        padding: 0 24px;
    }

    @media (min-width: 1024px) {
        height: $headerHeight + 10px;
    }

    .container-1000 {
        height: 100%;
        position: relative;
        display: flex;
        align-items: center;
    }

    .logo {
        img {
            width: 60px;

            @media (min-width: 768px) {
                width: 70px;
            }
            @media (min-width: 1024px) {
                width: 80px;
            }
        }
    }

    .hamburger {
        position: absolute;
        right: 0;

        @media (min-width: 1024px) {
            display: none;
        }
    }
}
