main#main {
    padding-top: $headerHeight;
    padding-bottom: $footerHeight;
    height: 100%;
    position: relative;
    transition: 0.7s;

    @media (min-width: 1024px) {
        padding-top: $headerHeight + 10px;
    }

    &.flip {
        transform: translateX(-100%);
    }
}

section#index {
    position: relative;
    background-color: white;

    article.bg-wrapper {
        margin-top: -$headerHeight;
        height: 100vh;
        background-image: url('https://images.unsplash.com/photo-1534805406629-e92418bcab97?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2700&q=80');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        overflow: hidden;

        @media (min-width: 1024px) {
            margin-top: -$headerHeight - 10px;
            background-attachment: fixed;
        }
    }
}
